import Vue from 'vue'
import { slugify } from './helper'
import Router from 'vue-router'
import Main from '@/components/Main'
import Wrapper from '@/components/Wrapper'
import miners from './miners'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    ...miners.map(m => ({
      path: slugify(m.name),
      name: m.name,
      component: Wrapper,
    })),
  ]
})



