<style>
.main {
  margin-top: 4em !important;
  justify-content: space-between !important;
}
</style>

<template>
  <div class="ui text main container cards">
    <Miner
      v-for="miner, key in miners"
      :xmrusd="xmrusd"
      :miner="miner"
      :key="key"
      :run="key === 0"
    />
  </div>
</template>

<script>
import Miner from '@/components/Miner'
import miners from '@/miners'

export default {
  data() {
    return {
      miners,
      xmrusd: 0,
    }
  },
  created() {
    this.fetchXMRPrice();
  },
  methods: {
    fetchXMRPrice() {
      const url = 'https://min-api.cryptocompare.com/data/price?fsym=XMR&tsyms=USD'
      this.$http.get(url).then(res => {
        console.log(res)
        this.xmrusd = res.body.USD
        console.log(this.xmrusd)
      });
    },
  },
  components: {
    Miner
  },
}
</script>


