<style lang="scss">
.card {
  width: 100% !important;
  .statistics {
    flex: 1 1 auto;
    justify-content: space-around;
    padding: 0 38px 0 0 !important;
    &:after {
      content: none !important;
    }
    .statistic {
      margin: 0 !important;
    }
  }
  .buttons {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: auto !important;
    .button {
      width: 38px !important;
      border-top-left-radius: 0 !important;
      border-bottom-left-radius: 0 !important;
    }
  }
}
</style>

<template>
  <div class="ui card">
    <div class="extra content">
      <a :href="miner.href" target="_blank">
        <i class="linkify icon"></i>
        {{ miner.name }}
      </a>
    </div>
    <div class="content">
      <sui-statistics-group class="small">
        <sui-statistic in-group>
          <sui-statistic-value>{{ speed.toFixed(2) }}</sui-statistic-value>
          <sui-statistic-label>hash/sec</sui-statistic-label>
        </sui-statistic>
        <sui-statistic in-group>
          <sui-statistic-value>{{ total }}</sui-statistic-value>
          <sui-statistic-label>total hash</sui-statistic-label>
        </sui-statistic>
        <sui-statistic in-group>
          <sui-statistic-value>{{ percent + '%' }}</sui-statistic-value>
          <sui-statistic-label>CPU usage</sui-statistic-label>
        </sui-statistic>
        <sui-statistic in-group>
          <sui-statistic-value>{{ usdmonth.toFixed(2) }}</sui-statistic-value>
          <sui-statistic-label>USD/month</sui-statistic-label>
        </sui-statistic>
      </sui-statistics-group>
    </div>
    <div class="ui vertical icon buttons">
      <sui-button @click.native="increase">
        <i class="plus icon"></i>
      </sui-button>
      <sui-button v-if="running" @click.native="stopMiner">
        <i class="pause icon"></i>
      </sui-button>
      <sui-button v-else @click.native="startMiner">
        <i class="play icon"></i>
      </sui-button>
      <sui-button @click.native="decrease">
        <i class="minus icon"></i>
      </sui-button>
    </div>
    <sui-progress attached bottom indicating :percent="percent"></sui-progress>
  </div>
</template>

<script>
import { slugify } from '@/helper'

export default {
  props: ['miner', 'xmrusd', 'run'],
  data() {
    return {
      speed: 0,
      total: 0,
      percent: 80,
      loaded: false,
      interval: null,
      running: this.run,
    }
  },
  created() {
    if (this.run) return this.loadScript()
  },
  computed: {
    xmr() {
      return this.hashesToXMR(this.total) || 0
    },
    usd() {
      return (this.xmr * this.xmrusd)
    },
    usdmonth() {
      const sec_in_month = (60 * 60 * 24 * 365)/12
      return (this.hashesToXMR(this.speed * sec_in_month) * this.xmrusd)
    },
    throttle() {
      return 1 - this.percent / 100;
    },
    slug() {
      return slugify(this.miner.name)
    }
  },
  methods: {
    loadScript() {
      ((d, script) => {
        script = d.createElement('script')
        script.type = 'text/javascript'
        script.async = true
        script.onload = () => {
          this.loaded = true
          this.startMiner()
        }
        script.src = this.miner.script
        d.getElementsByTagName('head')[0].appendChild(script)
      })(document)
    },
    increase() {
      if (this.percent < 100) {
        this.percent += 10;
        if (this.loaded) {
          this.miner.setThrottle(this.throttle)
        }
      }
    },
    decrease() {
      if (this.percent > 10) {
        this.percent -= 10;
        if (this.loaded) {
          this.miner.setThrottle(this.throttle)
        }
      }
    },
    hashesToXMR(hashes) {
      // 0.000052 XMR per 1 milion hashes
      return (hashes / 1000000) * 0.000052
    },
    startMiner() {
      if (!this.loaded) return this.loadScript()
      this.miner.start(this.throttle)
      this.running = true
      this.interval = setInterval(() => {
        this.speed = this.miner.getHashesPerSecond()
        this.total = this.miner.getTotalHashes()
      }, 1000)
    },
    stopMiner() {
      this.miner.stop()
      clearInterval(this.interval)
      this.speed = 0
      this.running = false
    },
  },
}
</script>



